.header {
  display: -ms-grid;
  margin: 0 auto;

  margin-top: 20px;
  overflow: hidden;

  /* interface/modal-bg */

  background: #1c1c21;
  /* card/x-large */

  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04),
    0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04),
    0px 0px 1px rgba(0, 0, 0, 0.04);
}

.nav {
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
}

.menu_left {
  display: flex;
  align-items: flex-start;
  
  transform: translateX(-10%);
  gap: 8px;
}

.menu_right {
  align-items: center;
  display: flex;
  margin-left: 13%;
}

.menu_item {
  display: flex;
  border-radius: 40px;
  cursor: pointer;

  /* navigation link/inactive */

  box-sizing: border-box;

  /* Auto layout */
  flex-direction: row;
  align-items: center;
  padding: 16px 20px;
  gap: 8px;

  height: 56px;
  left: 1073px;
  top: 0px;
}
.menu_item_a {
 color: aliceblue;
}

.logo {
  display: flex;
  align-self: center;

  max-width: 289.15px;
  max-height: 40px;
  left: 495px;
  top: 8px;
  border-radius: 40px;
}
