.container {
  padding-top: 2%;
  overflow-y: hidden;
  justify-content: space-evenly;
  max-width: 700px;
  display: table;
  margin: 0 auto;
}
.orderNumber {
  text-align: center;
}
.orderName {
  text-align: start;
  max-width: 640px;
}

.done {
  color: aliceblue;
}

.ingredient {
  background: linear-gradient(#131316, #131316) padding-box,
  linear-gradient(to right, #801ab2, #4c4cff) border-box;
  border: 2px solid transparent;
  border-radius: 80px;
  width: 64px;
  height: 64px;
  object-fit: cover;
}

.ingredientsList {
  
  margin-top: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 16px;
  height: 280px;
  overflow-y: auto;
  box-sizing: border-box;
}

.ingredient {
   overflow-y: hidden;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.image {
  display: flex;
   overflow-y: hidden;
}

.ingredientName {
  width: 400px;
  margin-top: 20px;
}

.orderPrice{
  display: flex;
 justify-content: end;
 transform: translateY(-210%);
 
}

.orderPriceDetails {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 9px;
  overflow-y: hidden;
}



